.hide {
  text-indent: -999999px;
  font-size: 0;
}

button, input, optgroup, select, textarea {
  font-family: 'Comfortaa', cursive;
}

.button {
  border: 0;
  background: $yellow;
  color: $black;
  font-family: 'Comfortaa', cursive;
  font-weight: 600;
}

.avatar {
  min-width: 50px;
  height: 50px;
  border-radius: 50%;
  align-self: center;
}

pre.error {
  padding: 5rem;
  background: white;
  line-height: 3;
  white-space: pre-line;
}



