.polls {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 5px;
}
.poll-card {
  border: 1px solid $purple;
  padding: 10px;
  opacity: 1;
  transition: all 1s;
  &.hidden {
    opacity: 0;
  }
  &.gone-forever {
    display: none;
  }
  .delete-form {
    &.hidden {
      display: none;
    }
  }
}

// Single Poll
.poll {
  form {
    display: flex;
    button {
      flex: 1;
      margin: 10px;
      background-color: $purple;
      border: 3px solid $purple;
      padding: 15px;
      color: $white;
      &:hover {
        background-color: $white;
        color: $purple;
        cursor: pointer;
      }
    }
  }
  .results {
    .result {
      display: inline-block;
      border: 1px solid $white;
      background-color: rgba(130, 124, 210, 0.3);
      text-align: center;
      padding: 20px;
      overflow: hidden;
      word-wrap: normal;
      &.empty {
        display: none;
      }
    }
  }
  form.store-option-form.hidden {
    display: none;
  }
}
